.backdrop {
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(84, 84, 84, .5);
  padding: 40px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden .modal {
  transform: scale(.1);
}

.modal-open {
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal-backdrop {
  width: 100%;
  height: 100%;
  background-color: #8c8c8c;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

